import { Fragment, useEffect, useState } from "react"
import RoutePanel from "../../components/RoutePanel"
import { Tabs, Tab, Box, makeStyles } from '@material-ui/core'
import TabPanel from "../../components/TabPanel"
import { useDispatch, useSelector } from "react-redux"
import { fetchProjects, fetchProjectsByTagName } from "../../redux/actions/project.actions"
import TagProject from "./tag.projects"
const Projects = () => {
   const classes = styles()
   const [value, setValue] = useState(0);
   const dispatch = useDispatch()
   const projects = useSelector(state => state.project.projects)
   useEffect(() => {
      if (value === 0) {
         dispatch(fetchProjects())
      }
      else if (value === 1) {
         dispatch(fetchProjectsByTagName('Bit th'))
      }
      else if (value === 2) {
         dispatch(fetchProjectsByTagName('Nh ph'))
      }
      else if (value === 3) {
         dispatch(fetchProjectsByTagName('Cn h'))
      }
      else {
         dispatch(fetchProjectsByTagName('Cc hng mc khc'))
      }
   }, [dispatch, value])
   const handleChange = (event, newValue) => {
      setValue(newValue)
   }
   return (
      <Fragment>
         <RoutePanel namePanel="D n" />
         <Box className={classes.tabContainer} sx={{ bgcolor: 'background.paper' }}>
            <Tabs
               className={classes.tabsWrapper}
               value={value}
               onChange={handleChange}
               indicatorColor="primary"
               textColor="primary"
               aria-label="scrollable auto tabs example"
            >
               <Tab label="Tt c" />
               <Tab label="Bit th" />
               <Tab label="Nh ph" />
               <Tab label="Cn h" />
               <Tab label="Cc hng mc khc" />
            </Tabs>
            <TabPanel value={value} index={0}>
               <TagProject projects={projects} />
            </TabPanel>
            <TabPanel value={value} index={1} >
               <TagProject projects={projects} />
            </TabPanel>
            <TabPanel value={value} index={2}>
               <TagProject projects={projects} />
            </TabPanel>
            <TabPanel value={value} index={3}>
               <TagProject projects={projects} />
            </TabPanel>
            <TabPanel value={value} index={4} >
               <TagProject projects={projects} />
            </TabPanel>
         </Box>
      </Fragment>
   )
}
const styles = makeStyles({
   tabContainer: {
      display: "flex",
      flexDirection: 'column',
   },
   tabsWrapper: {
      margin: "0 auto",
   }
})
export default Projects