import { Fragment, useEffect, useState } from "react"
import {
   Avatar, Button, FormControl,
   FormHelperText, Grid, makeStyles,
   Paper, TextField, Typography
} from "@material-ui/core"
import DashboardHeader from "../../layout/header/dashboard.header"
import jwt from 'jsonwebtoken'
import { DOMAIN } from "../../services/baseURL.services"


const Profile = () => {
   const classes = styles()
   const [profile, setProfile] = useState()

   useEffect(() => {
      let token = JSON.parse(localStorage.getItem('token'))
      setProfile(jwt.verify(token, "KIENHOA_APP"))
   }, [])
   const handleNameChange=( e)=>{
      setProfile({
         ...profile,
         [e.target.name]:e.target.value
      })
   }
   return profile ? (
      <Fragment>
         <DashboardHeader />
         <Grid container className={classes.profileWrapper} spacing={2}>
            <Grid item md={5} sm={12}>
               <Paper className={classes.infoWrapper}>
                  <Typography variant="h5" className={classes.title}>Cp nht thng tin</Typography>
                  <FormControl className={classes.formControl}>
                     <TextField label='Tn ng nhp' variant="filled"
                        disabled value={profile.username} fullWidth />
                     <FormHelperText>Tn ng nhp khng th cp nht c</FormHelperText>
                  </FormControl>
                  <Grid item>
                     <TextField label='Tn' name="firstName" style={{ margin: '0px 20px' }}
                      helperText="Cp nht Tn" onChange={handleNameChange}
                       defaultValue={profile.firstName} />
                     <TextField label='H' name="lastName"
                        onChange={handleNameChange}
                      helperText="Cp nht H" defaultValue={profile.lastName} />
                  </Grid>
                  <Grid item>
                     <FormControl className={classes.formControl}>
                        <TextField label="Phn loi ti khon" disabled variant="filled" value={profile.role} />
                        <FormHelperText>y l vai tr ca bn khng th cp nht c</FormHelperText>
                     </FormControl>
                  </Grid>
                  <Button color="primary" variant="contained" style={{ margin: '15px 0' }}>Cp nht thng tin</Button>
               </Paper>
            </Grid>
            <Grid item md={5} sm={12}>
               <Paper className={classes.avatarWrapper}>
                  <Typography className={classes.title} variant="h5">Cp nht nh i din</Typography>
                  <FormControl className={classes.formControl}>
                     <TextField onChange type="file" fullWidth/>
                     <FormHelperText>Chn nh i din</FormHelperText>
                     <Avatar
                        className={classes.previewAvatar}
                        src={DOMAIN + "/" + profile.avatar}
                        alt="nh i din" />
                     <FormHelperText id="preview-imge">nh xem trc</FormHelperText>
                     <Button variant="contained" color="primary"> Cp nht nh ai din</Button>
                  </FormControl>
               </Paper>
               <Paper className={classes.passwordWrapper}>
                  <Typography variant="h5" className={classes.title}>i mt khu</Typography>
                  <TextField label='Mt khu mi' helperText="Nhp mt khu mi" />
                  <Button color="primary" variant="contained" style={{ margin: "15px 0" }}>i mt khu</Button>
               </Paper>

            </Grid>

         </Grid>
      </Fragment>
   ) : null
}
const styles = makeStyles({
   profileWrapper: {
      width: "100vw",
      height: "80vh",
      justifyContent: "center",
      alignItems: 'center',
   },
   title: {
      textAlign: 'center'
   },
   infoWrapper: {
      height: '100%',
      display: 'flex',
      flexDirection: "column",
      padding: '10px 20px',
   },
   previewAvatar: {
      width: '60px',
      height: "60px"
   },
   passwordWrapper: {
      padding: '20px 25px',
      display: 'flex',
      flexDirection: 'column',
   },
   formControl: {
      margin: "15px"
   }
})
export default Profile